/* Begin: display a small horizontal bar before footnote elements if they
 * exist. */
div.footnotes {
  position: relative;
  display: inline-block;
  margin-top: 20px;
}

div.footnotes:before {
  /* Element only present when there are footnotes. */
  content: '';
  width: 50%;
  left: 25%;
  height: 2px;
  background: #b0b0b0;
  position: absolute;
  top: -1px;
}
/* End horizontal bar. */

a.footnote:before {
 content: "[";
}

a.footnote:after {
    content: "]";
}
figure object, figure svg, figure img {
  max-width: 100%;
  display: block;
  margin: auto;
}
figure figcaption {
  margin-top: 10px;
}
figcaption::before {
    counter-increment: figures;
    content: "Fig. " counter(figures) " - ";
    font-weight: bold;
}

/* gojekyll includes an <hr> in footnotes that we don't want */
div.footnotes hr {
  display: none;
}
